@charset "UTF-8";
:root {
  --inner-padding: 30px;
}

:root {
  --font-family-base: "Noto Sans JP", sans-serif;
  --font-family-second: "Shippori Mincho B1", serif;
  --font-family-third: "Shippori Mincho", serif;
  --font-family-fourth: "Noto Sans", sans-serif;
}

:root {
  --font-weight-regular: 400;
  --font-weight-normal: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  --font-weight-extra-bold: 800;
}

:root {
  --black: #000;
  --white: #fff;
  --text-black: #111;
  --sub-title-gray: #4D4D4D;
  --text-gray: #727272;
  --main-blue: #0F5779;
  --main-orange: #DA9B2D;
  --main-red: #AB4545;
  --main-green: #7B6F2F;
  --main-yellow: #D3BC5F;
}

:root {
  --z-index-header: 100;
  --z-index-to-top: 200;
  --z-index-drawer: 1000;
  --z-index-hamburger: 2000;
  --z-index-modal: 10000;
}

:root {
  --cq-ref-mobile: 390;
  --cq-ref: 1366;
  --fluid-basis-mobile: (100vw - 320px) / (375 - 320);
  --fluid-basis: (100vw - 768px) / (1366 - 768);
  --fluid-basis-2: (100vw - 1366px) / (1800 - 1366);
}

:root {
  --duration-short: 0.2s;
  --duration-base: 0.3s;
  --duration-semi-long: 0.5s;
  --duration-long: 0.7s;
  --ease-smooth-bounce: cubic-bezier(.48, .07, .33, .79);
}

body {
  color: var(--text-black);
  font-family: var(--font-family-base);
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.7204301075vw;
  }
}
@media (min-width: 930px) {
  html {
    font-size: 16px;
  }
}

.bg-body {
  background: url("../images/common/body-bg-sp.webp") no-repeat center top/cover;
}
@media screen and (min-width: 768px) {
  .bg-body {
    background: url("../images/common/body-bg.webp") no-repeat center top/cover;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  overflow-wrap: anywhere;
  word-break: normal;
  line-break: strict;
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul,
ol,
button,
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd,
time,
small {
  margin: 0;
  padding: 0;
}

ul,
ol {
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

a,
span,
button {
  display: inline-block;
}

small,
time,
figure {
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

address {
  font-style: normal;
}

img {
  max-width: 100%;
  display: block;
  width: 100%;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  clip-path: polygon(0 0, 0 0, 0 0, 0 0);
  border: 0;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
  transition: 0.3s;
}

button {
  color: inherit;
}

@media (any-hover: hover) {
  a:hover {
    cursor: pointer;
  }
}
@media (min-width: 768px) {
  a[href*="tel:"] {
    pointer-events: none;
    cursor: default;
    text-decoration: none;
  }
}
a {
  -webkit-tap-highlight-color: transparent;
}

button {
  -webkit-tap-highlight-color: transparent;
}

