.mv__inner {
  display: grid;
  grid-template-columns: 21fr 294fr;
  padding-left: calc(45 * 100 / var(--cq-ref-mobile) * 1cqw);
}
@media (max-width: 767px) {
  .mv__inner {
    gap: calc(30 * 100 / var(--cq-ref-mobile) * 1cqw);
  }
}
@media screen and (min-width: 768px) {
  .mv__inner {
    grid-template-columns: 90fr 21fr 752fr;
    padding-inline: clamp(59px, 59px + 80 * var(--fluid-basis), 139px) clamp(51px, 51px + 120 * var(--fluid-basis), 171px);
  }
}
@media screen and (min-width: 1366px) {
