  margin-top: calc(62 * 100 / var(--cq-ref) * 1cqw);
}

.mv__nav-link {
  color: var(--text-gray);
  font-family: var(--font-family-second);
  font-size: max(12px, 15 * 100 / var(--cq-ref) * 1cqw);
  font-weight: var(--font-weight-bold);
  line-height: 1.5;
  white-space: nowrap;
  position: relative;
  padding-bottom: 0.0625rem;
}

.mv__nav-link::before {
  content: "";
  position: absolute;
  left: 50%;
  translate: -50% 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: currentColor;
  opacity: 0.5;
  transform: scale(0, 1);
  transform-origin: right top;
  transition: transform 0.6s cubic-bezier(0.6, 0, 0, 1);
}

.mv__nav-link:hover::before {
  transform: scale(1, 1);
  transform-origin: left top;
}

.mv__text {
  writing-mode: vertical-rl;
  color: var(--text-gray);
  font-feature-settings: "vert" on;
  font-family: var(--font-family-third);
  font-size: 20px;
